body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #293133;
  font-family: "Lato", sans-serif;
}

.App {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#background {
  height: 100%;
  width: 100%;
  background-image: url(/fcc_frontend_drum_machine/static/media/concert.ef060931.jpg);
  background-size: cover;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

#drum-machine {
  position: absolute;
  max-width: 550px;
  margin: auto;
}

.drum-pad {
  width: 100px;
  height: 100px;
  margin: 20px;
  border-radius: 5%;
  border: none;
  box-shadow: 3px 3px 20px;
  font-size: 25px;
  background-color: rgba(255,244,0,1);
}

.drum-pad:focus {
  outline: none;
}

#display {
  height: 50px;
  line-height: 50px;
  text-align: center;
  margin: 40px;
  font-size: 25px;
  background-color: white;
  box-shadow: 3px 3px 20px black;
  border-radius: 2px;
}

#button-container {
  display: grid;
  grid-template: 33% 33% 33% / 33% 33% 33%;
  justify-items: center;
}

@media screen and (max-width: 400px) {
  .drum-pad {
    width: 70px;
    height: 70px;
    margin: 10px;
  }
}
